@import './variables.scss';

@mixin mobile() {
  @media only all and (max-width: $mobile-size) {
    @content;
  }
}

@mixin xsmall() {
  @media only all and (max-width: $xsmall-size) {
    @content;
  }
}

@mixin large() {
  @media only all and (max-width: $large-size) {
    @content;
  }
}

@mixin middle() {
  @media only all and (max-width: $middle-size) {
    @content;
  }
}
