@import './variables.scss';
@import './mixins.scss';

.text {
  font-weight: 400;
  color: $main-text;
  font-size: 16px;

  &--bold {
    font-weight: 700;
  }

  &--white {
    color: $white;
  }
}

h5 {
  font-weight: 700;
  font-size: 14px;
}

h4 {
  font-weight: 700;
  font-size: 16px;
}
